import React, { useState, useEffect } from 'react';
import { useNumeroPolicia } from '../../NumeroPoliciaContext';
import ScannerInput from '../ui/qrScanner/ScannerInput';
import ConfirmPopup from '../ui/confirmar/ConfirmPopup';
import Header from '../header/Header';

import './Ingreso.css';

const Ingreso = () => {
    const { numeroPolicia, nombrePolicia } = useNumeroPolicia();
    const [data, setData] = useState([]);
    const [showPopup, setShowPopup] = useState(false);
    const [qrData, setQRData] = useState('');
    const [showForm, setShowForm] = useState(false);

    // Estado para controlar la asistencia de cada familiar
    const [asistenciaFamiliar, setAsistenciaFamiliar] = useState({});

    const handleConfirm = () => {
        setShowPopup(false);

        const familiaresSeleccionados = data.filter((familiar) => asistenciaFamiliar[familiar.id]);
        const idsFamiliaresSeleccionados = familiaresSeleccionados.map((familiar) => familiar.id);

        const registroData = {
            nombre_policia: nombrePolicia,
            numero_policia: numeroPolicia,
            personas_familiares: idsFamiliaresSeleccionados.join(','),
            activo: true,
        };

        fetch('http://localhost:3000/api/v1/casapolicia', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(registroData),
        })
            .then((response) => response.json())
            .then((responseData) => {
                console.log('Registro creado:', responseData);
            })
            .catch((error) => {
                console.error('Error al crear el registro:', error);
            });

        setShowForm(false);
    };

    const handleCancel = () => {
        setShowPopup(false);
    };

    const handleDataSubmit = (data) => {
        console.log('Datos ingresados:', data);
        setQRData(data);
        setShowForm(true);
    };

    useEffect(() => {
        if (numeroPolicia) {
            const apiUrl = `http://localhost:3000/api/v1/familiar/police/${numeroPolicia}`;

            fetch(apiUrl)
                .then((response) => response.json())
                .then((data) => {
                    setData(data);
                })
                .catch((error) => {
                    console.error('Error al obtener los datos:', error);
                });
        }
    }, [numeroPolicia]);

    // Función para manejar el cambio de asistencia de un familiar
    const handleAsistenciaChange = (id) => {
        setAsistenciaFamiliar({
            ...asistenciaFamiliar,
            [id]: !asistenciaFamiliar[id],
        });
    };

    // Filtrar los registros donde eliminado sea igual a false
    const registrosFiltrados = data.filter((familiar) => familiar.eliminado === false);

    return (
        <div className="ingreso-container">
            <Header className="Header" />
            <div className='main-content'>
                <h2>Ingreso</h2>
                <div className='registro-fomularios'>
                    {!showForm ? (
                        <ScannerInput onDataSubmit={handleDataSubmit} />
                    ) : (
                        <div className="ingreso-tabla-visitas">
                            <h4>Si la persona viene con alguien más, selecciónelo. Si no, solo presione el botón "Registrar entrada"</h4>
                            <div className="ingreso-tabla-encabezado ingresos-titulos-tabla">
                                <div className="main-column main-encabezado">Nombre</div>
                                <div className="main-column main-encabezado">Observación</div>
                                <div className="main-column main-encabezado">Documentos faltantes</div>
                                <div className="main-column main-encabezado">Tipo</div>
                                <div className="main-column main-encabezado">Asistencia</div>
                            </div>
                            {registrosFiltrados.map((familiar) => (
                                <div className="ingreso-tabla-encabezado" key={familiar.id}>
                                    <div className="main-column">{familiar.nombres} {familiar.apellido1} {familiar.apellido2}</div>
                                    <div className="main-column">{familiar.observacion}</div>
                                    <div className="main-column">
                                        {familiar.tipo_familiar
                                            ? (familiar.ine === false ? '"INE" ' : '') + (familiar.certificado_medico === false ? '"Certificado médico" ' : '')
                                            : (familiar.acta_nacimiento === false ? '"Acta de nacimiento" ' : '') + (familiar.certificado_medico === false ? '"Certificado médico" ' : '')
                                        }
                                    </div>
                                    <div className="main-column">{familiar.tipo_familiar ? "Pareja" : "Familiar"}</div>
                                    <div className="main-column">
                                        <input
                                            className="ingreso-btnCheckout"
                                            type="checkbox"
                                            checked={asistenciaFamiliar[familiar.id] || false}
                                            onChange={() => handleAsistenciaChange(familiar.id)}
                                        />
                                    </div>
                                </div>
                            ))}
                            <button className='ingreso-input-button' onClick={() => setShowPopup(true)}>
                                Registrar entrada
                            </button>
                            {showPopup && (
                                <ConfirmPopup
                                    message="¿Realizar registro?"
                                    onConfirm={handleConfirm}
                                    onCancel={handleCancel}
                                />
                            )}
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
};

export default Ingreso;
