import React, { useState, useEffect } from 'react';
import { Line } from 'react-chartjs-2';
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend,
    Filler,
} from 'chart.js';

ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend,
    Filler
);

export default function LinesChartSalida() {
    const [data, setData] = useState([]);
    const [horas, setHoras] = useState([]);
    const [registrosPorHora, setRegistrosPorHora] = useState([]);

    useEffect(() => {
        // Realiza la solicitud GET a la API para obtener los registros activos
        fetch('http://localhost:3000/api/v1/casapolicia')
            .then((response) => response.json())
            .then((data) => {
                // Filtra los registros con activo=false
                const registrosInactivos = data.filter((registro) => !registro.activo);

                // Obtener los datos necesarios para el gráfico
                const horas = Array.from({ length: 24 }, (_, i) => i);
                const registrosPorHora = Array(24).fill(0);
                registrosInactivos.forEach((registro) => {
                    const fecha = new Date(registro.updatedAt);
                    const hora = fecha.getHours();
                    registrosPorHora[hora] += 1;
                });
                setHoras(horas);
                setRegistrosPorHora(registrosPorHora);
                setData(registrosInactivos);
            })
            .catch((error) => {
                console.error('Error al obtener los datos:', error);
            });
    }, []);

    const chartData = {
        labels: horas.map((hora) => `${hora}:00`),
        datasets: [
            {
                label: 'Registros por hora de salida',
                data: registrosPorHora,
                tension: 0.5,
                fill: true,
                borderColor: 'rgb(200, 0, 0)', // Cambiar a rojo más oscuro
                backgroundColor: 'rgba(200, 0, 0, 0.5)', // Cambiar a rojo más oscuro
                pointRadius: 5,
                pointBorderColor: 'rgba(200, 0, 0)', // Cambiar a rojo más oscuro
                pointBackgroundColor: 'rgba(200, 0, 0)', // Cambiar a rojo más oscuro
            },
        ],
    };

    const chartOptions = {
        scales: {
            y: {
                min: 0,
            },
            x: {
                ticks: { color: 'rgba(200, 0, 0)' }, // Cambiar a rojo más oscuro
            },
        },
    };


    return <Line data={chartData} options={chartOptions} />;
}
