import React, { useState } from 'react';
import AlertWindow from '../alerwindow/AlertWindow';
import { useNumeroPolicia } from '../../../NumeroPoliciaContext';
import './FamiliarForm.css';

import correctoImg from '../../assets/correcto2.png';

const FamiliarForm = () => {
    const [registroGuardado, setRegistroGuardado] = useState(false);
    const [nombre, setNombre] = useState('');
    const [apellido1, setApellido1] = useState('');
    const [apellido2, setApellido2] = useState('');
    const [observaciones, setObservaciones] = useState('Ninguna');
    const [certificadoMedicoChecked, setCertificadoMedicoChecked] = useState(false);
    const [actaNacimientoChecked, setActaNacimientoChecked] = useState(false);

    // Obtiene el número de policía del contexto
    const { numeroPolicia } = useNumeroPolicia();

    // Agrega un estado para controlar la visibilidad de la ventana de alerta
    const [mostrarAlerta, setMostrarAlerta] = useState(false);

    const handleFormSubmit = (e) => {
        e.preventDefault();
        // Verifica si los campos de texto están vacíos
        if (nombre.trim() === '' || apellido1.trim() === '' || apellido2.trim() === '') {
            // Si los campos no están llenados correctamente, muestra la ventana de alerta
            setMostrarAlerta(true);
        } else {
            const numeroPoliciaInt = parseInt(numeroPolicia);
            console.log("numero de policia en bd es:" + numeroPolicia);
            // Crea un objeto con los datos del formulario y el número de policía del contexto
            const nuevoRegistro = {
                nombres: nombre,
                apellido1,
                apellido2,
                observacion: observaciones,
                ine: false,
                certificado_medico: certificadoMedicoChecked,
                acta_nacimiento: actaNacimientoChecked,
                tipo_familiar: false,
                numero_policia: numeroPoliciaInt,
                eliminado: false,
            };

            // Realiza una solicitud POST para guardar el registro en la API
            fetch('http://localhost:3000/api/v1/familiar', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(nuevoRegistro),
            })
                .then((response) => response.json())
                .then((data) => {
                    // Cambia el estado para mostrar el mensaje de registro guardado
                    setRegistroGuardado(true);
                })
                .catch((error) => {
                    console.error('Error al guardar el registro:', error);
                });
        }
    };

    // Agrega un manejador para cerrar la ventana de alerta
    const handleCloseAlert = () => {
        setMostrarAlerta(false);
    };

    return (
        <div className="familiar-form-container">
            <h2 className="familiar-form-label">Registro de Familiar</h2>
            {registroGuardado ? (
                <div className="familiar-registro-guardado-exitosamente">
                    Registro Guardado Exitosamente
                    <img className='familiar-img-correcto' src={correctoImg} alt="Correcto" />
                </div>
            ) : (
                <form className="familiar-form-fomularios" onSubmit={handleFormSubmit}>
                    {/* Campos de entrada */}
                    <div className="familiar-form-inputs-container">
                        <input
                            type="text"
                            placeholder="Nombre(s)"
                            value={nombre}
                            onChange={(e) => setNombre(e.target.value)}
                            className="familiar-form-name-input-names"
                        />
                        <input
                            type="text"
                            placeholder="Apellido Paterno"
                            value={apellido1}
                            onChange={(e) => setApellido1(e.target.value)}
                            className="familiar-form-name-input-names"
                        />
                        <input
                            type="text"
                            placeholder="Apellido Materno"
                            value={apellido2}
                            onChange={(e) => setApellido2(e.target.value)}
                            className="familiar-form-name-input-names"
                        />
                        <input
                            type="text"
                            placeholder="Observaciones"
                            value={observaciones}
                            onChange={(e) => setObservaciones(e.target.value)}
                            className="familiar-form-name-input-names"
                        />
                    </div>
                    <div className="familiar-name-form-inputs-label">Documentos:</div>
                    <div className="name-form-inputs-fam">
                        <label className="name-form-inputs-fam-opc">
                            <input
                                type="checkbox"
                                checked={certificadoMedicoChecked}
                                onChange={() => setCertificadoMedicoChecked(!certificadoMedicoChecked)}
                            /> Certificado Médico Oficial
                        </label>
                        <label className="name-form-inputs-fam-opc">
                            <input
                                type="checkbox"
                                checked={actaNacimientoChecked}
                                onChange={() => setActaNacimientoChecked(!actaNacimientoChecked)}
                            /> Acta de Nacimiento
                        </label>
                    </div>
                    <button type="button" className="name-form-button-familiar" onClick={handleFormSubmit}>
                        Guardar Registro
                    </button>
                </form>
            )}
            {/* Muestra la ventana de alerta si mostrarAlerta es true */}
            {mostrarAlerta && (
                <AlertWindow
                    message="Por favor, completa todos los campos de texto del formulario 'Familia'"
                    onClose={handleCloseAlert}
                />
            )}
        </div>
    );
};

export default FamiliarForm;
