import React, { useState } from 'react';
import Header from '../header/Header';
import ScannerInput from '../ui/qrScanner/ScannerInput';

import CreateRegistro from '../ui/CRUD/CreateRegistro/CreateRegistro';
import ViewRegistros from '../ui/CRUD/ViewRegistros/ViewRegistros';
import UpdateRegistros from '../ui/CRUD/UpdateRegistros/UpdateRegistros';
import DeleteRegistros from '../ui/CRUD/DeleteRegistros/DeleteRegistros';


import './Crud.css';

const Crud = () => {
    const [qrData, setQRData] = useState('');
    const [showForm, setShowForm] = useState(false);
    const [selectedOption, setSelectedOption] = useState(null); // Nuevo estado para la opción seleccionada

    const handleDataSubmit = (data) => {
        console.log('Datos ingresados:', data);
        setQRData(data);
        setShowForm(true);
    };

    // Funciones para manejar las opciones seleccionadas
    const handleCrearNuevoRegistro = () => {
        setSelectedOption('crear');
    };

    const handleVerRegistros = () => {
        setSelectedOption('ver');
    };

    const handleActualizarRegistros = () => {
        setSelectedOption('actualizar');
    };

    const handleEliminarRegistros = () => {
        setSelectedOption('eliminar');
    };

    // Renderizado condicional basado en la opción seleccionada
    let content = null;

    if (showForm) {
        switch (selectedOption) {
            case 'crear':
                content = <CreateRegistro qrData={qrData} />;
                break;
            case 'ver':
                content = <ViewRegistros />;
                break;
            case 'actualizar':
                content = <UpdateRegistros />;
                break;
            case 'eliminar':
                content = <DeleteRegistros />;
                break;
            default:
                content = <h3>Seleccione una opción</h3>;
        }
    }

    return (
        <div className="crud-container">
            <Header className="Header" />
            <div className='main-content'>
                <h2>Editar Registros</h2>
                <div className='crud-fomularios'>
                    {!showForm ? (
                        <ScannerInput onDataSubmit={handleDataSubmit} />
                    ) : (
                        <div className="crud-formularios-menu">
                            <button onClick={handleCrearNuevoRegistro}>Crear Nuevo Registro</button>
                            <button onClick={handleVerRegistros}>Ver Registros</button>
                            <button onClick={handleActualizarRegistros}>Actualizar Registros</button>
                            <button onClick={handleEliminarRegistros}>Eliminar Registros</button>
                        </div>
                    )}
                </div>
                {content} {/* Aquí se renderiza el componente seleccionado */}
            </div>
        </div>
    );
};

export default Crud;
