import React, { useState, useEffect } from 'react';
import Header from '../header/Header';
import exitIco from '../assets/exit2.png';
import ConfirmPopup from '../ui/confirmar/ConfirmPopup';

import LinesChartEntrada from '../../LinesChartEntrada';
import LinesChartSalida from '../../LinesChartSalida';

import './Main.css';

const Main = () => {
    const [data, setData] = useState([]);
    const [showPopup, setShowPopup] = useState(-1);
    const [noRegistros, setNoRegistros] = useState(false); // Agregamos estado para mostrar mensaje de no registros

    const handleConfirm = (index) => {
        // Obtener el ID del registro seleccionado
        const registroId = data[index].id;

        // Realizar la solicitud PUT a la API para actualizar el registro
        fetch(`http://localhost:3000/api/v1/casapolicia/${registroId}`, {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ activo: false }), // Cambiar el valor de activo a false
        })
            .then((response) => response.json())
            .then(() => {
                // Lógica para actualizar la interfaz o realizar otras acciones si es necesario
                // Luego, eliminar el registro de la interfaz
                const updatedDatos = [...data];
                updatedDatos.splice(index, 1);
                setData(updatedDatos);
                setShowPopup(-1); // Volvemos a establecer -1 para cerrar el popup

                // Si no quedan registros, mostramos el mensaje de no registros
                if (updatedDatos.length === 0) {
                    setNoRegistros(true);
                }
            })
            .catch((error) => {
                console.error('Error al actualizar el registro:', error);
            });
    };

    const handleCancel = () => {
        // Lógica para cancelar acción
        setShowPopup(-1); // Volvemos a establecer -1 para cerrar el popup
    };

    const formatHora = (fechaHoraISO) => {
        const fechaHora = new Date(fechaHoraISO);
        return fechaHora.toLocaleString('en-US', {
            hour: 'numeric',
            minute: 'numeric',
            hour12: true,
        });
    };

    // Función para contar las personas
    const contarPersonas = (ids) => {
        if (!ids || ids.trim() === '') {
            return 'ingreso sol@ ';
        }

        const idArray = ids.split(',').map((id) => parseInt(id));
        const numPersonas = idArray.length;

        return numPersonas === 1 ? '1 persona' : `${numPersonas} personas`;
    };

    useEffect(() => {
        // Realiza la solicitud GET a la API para obtener los registros activos
        fetch('http://localhost:3000/api/v1/casapolicia')
            .then((response) => response.json())
            .then((data) => {
                // Filtra los registros con activo=true
                const registrosActivos = data.filter((registro) => registro.activo);
                setData(registrosActivos);

                // Si no hay registros, mostramos el mensaje de no registros
                if (registrosActivos.length === 0) {
                    setNoRegistros(true);
                }
            })
            .catch((error) => {
                console.error('Error al obtener los datos:', error);
            });
    }, []);

    return (
        <main className='main'>
            <Header className="Header" />
            <div className='main-content'>
                <h2>Principal</h2>
                <div className="grafico-linea-container">
                    <div className="grafico-linea">
                        <LinesChartEntrada className="grafico-linea"></LinesChartEntrada>
                    </div>
                    <div className="grafico-linea">
                        <LinesChartSalida className="grafico-linea"></LinesChartSalida>
                    </div>
                </div>
                {noRegistros ? ( // Mostramos mensaje de no registros si no hay datos
                    <div className='no-registros-msg'>No hay registros para mostrar.</div>
                ) : (
                    <div className='main-container'>
                        <div className="main-tabla-visitantes">
                            <div className="main-table-encabezado main-titulos-tabla">
                                <div className="main-column main-encabezado">Nombre Policia</div>
                                <div className="main-column main-encabezado">Hora Entrada</div>
                                <div className="main-column main-encabezado">Personas</div>
                                <div className="main-column main-encabezado">Salida</div>
                            </div>
                            {data.map((registro, index) => (
                                <div className="main-table-encabezado" key={index}>
                                    <div className="main-column">{registro.nombre_policia}</div>
                                    <div className="main-column">{formatHora(registro.createdAt)}</div>
                                    <div className="main-column">{contarPersonas(registro.personas_familiares)}</div>
                                    <div className="main-column">
                                        <button className="main-btnCheckout" onClick={() => setShowPopup(index)}>
                                            <img className='main-ico' src={exitIco} alt="Dar salida"></img>
                                        </button>
                                        {showPopup === index && (
                                            <ConfirmPopup
                                                message="¿Confirmar salida?"
                                                onConfirm={() => handleConfirm(index)}
                                                onCancel={handleCancel}
                                            />
                                        )}
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                )}
            </div>
        </main>
    );
};

export default Main;
