import React, { useState, useEffect } from 'react';
import { Line } from 'react-chartjs-2';
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend,
    Filler,
} from 'chart.js';

ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend,
    Filler
);

export default function LinesChartPorDia() {
    const [data, setData] = useState([]);
    const [dias, setDias] = useState([]);
    const [registrosPorDia, setRegistrosPorDia] = useState([]);

    useEffect(() => {
        // Realiza la solicitud GET a la API para obtener los registros activos
        fetch('http://localhost:3000/api/v1/casapolicia')
            .then((response) => response.json())
            .then((data) => {
                // Filtra los registros del mes actual
                const fechaActual = new Date();
                const mesActual = fechaActual.getMonth() + 1;
                const registrosDelMes = data.filter((registro) => {
                    const fechaRegistro = new Date(registro.createdAt);
                    return fechaRegistro.getMonth() + 1 === mesActual;
                });

                // Obtener los datos necesarios para el gráfico
                const dias = Array.from({ length: 31 }, (_, i) => i + 1);
                const registrosPorDia = Array(31).fill(0);
                registrosDelMes.forEach((registro) => {
                    const fecha = new Date(registro.createdAt);
                    const dia = fecha.getDate();
                    registrosPorDia[dia - 1] += 1;
                });
                setDias(dias);
                setRegistrosPorDia(registrosPorDia);
                setData(registrosDelMes);
            })
            .catch((error) => {
                console.error('Error al obtener los datos:', error);
            });
    }, []);

    const chartData = {
        labels: dias.map((dia) => `${dia}`),
        datasets: [
            {
                label: 'Registros por día del mes',
                data: registrosPorDia,
                tension: 0.5,
                fill: true,
                borderColor: 'rgb(255, 140, 0)', // Cambiar a naranja más oscuro
                backgroundColor: 'rgba(255, 140, 0, 0.5)', // Cambiar a naranja más oscuro
                pointRadius: 5,
                pointBorderColor: 'rgba(255, 140, 0)', // Cambiar a naranja más oscuro
                pointBackgroundColor: 'rgba(255, 140, 0)', // Cambiar a naranja más oscuro
            },
        ],
    };

    const chartOptions = {
        scales: {
            y: {
                min: 0,
            },
            x: {
                ticks: { color: 'rgba(255, 140, 0)' }, // Cambiar a naranja más oscuro
            },
        },
    };

    return <Line data={chartData} options={chartOptions} />;
}
