import React, { useState } from 'react';
import { Routes, Route, Navigate, useNavigate } from 'react-router-dom';

import Login from './components/login/Login';

import Main from './components/main/Main';
import Default from './components/default/Default';
import Bitacora from './components/bitacora/Bitacora';
import Ingreso from './components/ingreso/Ingreso';
import Crud from './components/crud/Crud';

import './App.css';

function App() {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [accessToken, setAccessToken] = useState(sessionStorage.getItem("accessToken"));
  const navigate = useNavigate();
  const handleLogin = () => {
    const expires_in = !sessionStorage.getItem("expires_in") ? Number(0) : sessionStorage.getItem("expires_in");
    const today = Date.now();
    const flag = today - expires_in;
    if (!accessToken || (expires_in === 0 || flag > 0)) {
      setIsLoggedIn(true);
    } else {
      console.log("todo mal bro")
      setIsLoggedIn(false);
    }
  };
  return (
    <div className='app-main'>
      <Routes>
        <Route
          path="/"
          element={!isLoggedIn && <Login setAccessToken={setAccessToken} onLogin={handleLogin} />}
        />
        <Route
          path="/main"
          element={!isLoggedIn ? <Main /> : <Navigate to="/" />}
        />
        <Route
          path="/ingreso"
          element={!isLoggedIn ? <Ingreso /> : <Navigate to="/" />}
        />
        <Route
          path="/bitacora"
          element={!isLoggedIn ? <Bitacora /> : <Navigate to="/" />}
        />
        <Route
          path="/crud"
          element={!isLoggedIn ? <Crud /> : <Navigate to="/" />}
        />
        <Route
          path="*"
          element={<Default />}
        />
      </Routes>
      <div className="footer" />
    </div>
  );
}

export default App;
