import React from 'react';
import { Outlet, Link } from 'react-router-dom';

import './Header.css'

import Logo from '../assets/svg/policia.svg';

const Header = () => {
    return (
        <header className="header">

            <Link to="/main">
                <button className="header-main-nav">
                    <div className="header-img-titulo">
                        <img className="header-logo" src={Logo} alt="logo" />
                        <div className="header-titulo">Visitas Casa Policia Morelia</div>
                    </div>
                </button>
            </Link>
            <div className='header-nav'>

                <Link to="/ingreso">
                    <button className="header-nav-bt">Ingreso</button>
                </Link>
                <Link to="/bitacora">
                    <button className="header-nav-bt">Bitacora</button>
                </Link>
                <Link to="/crud">
                    <button className="header-nav-bt">Editar registros</button>
                </Link>
            </div>
            <Link to="/">
                <button className="header-button">Cerrar Sesión</button>
            </Link>
            <Outlet />
        </header>
    );
};

export default Header;
