// NumeroPoliciaContext.js
import React, { createContext, useState, useContext } from 'react';

// Crea el contexto
const NumeroPoliciaContext = createContext();

// Crea un proveedor del contexto
export const NumeroPoliciaProvider = ({ children }) => {
    const [numeroPolicia, setNumeroPolicia] = useState(null);
    const [nombrePolicia, setNombrePolicia] = useState(null); // Agrega el nombre del policía

    return (
        <NumeroPoliciaContext.Provider value={{ numeroPolicia, setNumeroPolicia, nombrePolicia, setNombrePolicia }}> {/* Incluye nombrePolicia y setNombrePolicia en el valor proporcionado */}
            {children}
        </NumeroPoliciaContext.Provider>
    );
};

// Crea un hook personalizado para acceder al contexto
export const useNumeroPolicia = () => {
    const context = useContext(NumeroPoliciaContext);
    if (!context) {
        throw new Error('useNumeroPolicia debe ser utilizado dentro de un NumeroPoliciaProvider');
    }
    return context;
};
