import React, { useState, useEffect } from 'react';
import RegistroDetalles from '../ui/registrodetails/RegistroDetalles';
import Header from '../header/Header';
import LinesChartPorDia from '../../LinesChartPorDia';
import LinesChartPorMes from '../../LinesChartPorMes';
import './Bitacora.css';
import detailsIco from '../assets/details2.png';

const Bitacora = () => {
    const [registros, setRegistros] = useState([]);
    const [mostrarDetalles, setMostrarDetalles] = useState(false);
    const [registroSeleccionado, setRegistroSeleccionado] = useState(null);
    const [filtroNombre, setFiltroNombre] = useState('');
    const [filtroNumeroPolicia, setFiltroNumeroPolicia] = useState('');
    const [filtroFecha, setFiltroFecha] = useState('');
    const [noRegistros, setNoRegistros] = useState(false); // Agregamos estado para mostrar mensaje de no registros

    const mostrarDetallesRegistro = (registro) => {
        setRegistroSeleccionado(registro);
        setMostrarDetalles(true);
    };

    const cerrarDetalles = () => {
        setRegistroSeleccionado(null);
        setMostrarDetalles(false);
    };

    const formatFecha = (fechaISO) => {
        const fecha = new Date(fechaISO);
        const fechaFormateada = `${fecha.getFullYear()}/${padZero(fecha.getMonth() + 1)}/${padZero(fecha.getDate())} a las ${padZero(fecha.getHours())}:${padZero(fecha.getMinutes())}`;
        return fechaFormateada;
    };

    const padZero = (num) => {
        return num < 10 ? `0${num}` : num;
    };

    const contarPersonas = (ids) => {
        if (!ids || ids.trim() === '') {
            return 'ingreso sol@';
        }

        const idArray = ids.split(',').map((id) => parseInt(id));
        const numPersonas = idArray.length;

        return numPersonas === 1 ? '1 persona' : `${numPersonas} personas`;
    };
    // ...

    useEffect(() => {
        fetch('http://localhost:3000/api/v1/casapolicia')
            .then((response) => response.json())
            .then((data) => {
                let registrosFiltrados = data.filter((registro) => registro.activo === false);

                if (filtroNombre) {
                    registrosFiltrados = registrosFiltrados.filter((registro) =>
                        registro.nombre_policia.toLowerCase().includes(filtroNombre.toLowerCase())
                    );
                }

                if (filtroNumeroPolicia) {
                    registrosFiltrados = registrosFiltrados.filter((registro) =>
                        registro.numero_policia.includes(filtroNumeroPolicia)
                    );
                }

                if (filtroFecha) {
                    // Resta 2 días al valor de filtroFecha
                    const fechaFiltro = new Date(filtroFecha);
                    fechaFiltro.setDate(fechaFiltro.getDate() + 1);

                    registrosFiltrados = registrosFiltrados.filter((registro) => {
                        const fechaRegistro = new Date(registro.createdAt);
                        return fechaRegistro.toDateString() === fechaFiltro.toDateString();
                    });
                }

                // Ordenar los registros por fecha en orden descendente
                registrosFiltrados.sort((a, b) => new Date(b.updatedAt) - new Date(a.updatedAt));

                setRegistros(registrosFiltrados);

                // Verificar si todos los campos de filtro están vacíos
                if (!filtroNombre && !filtroNumeroPolicia && !filtroFecha) {
                    setNoRegistros(false);
                } else {
                    // Si no hay registros después de filtrar, mostrar mensaje de no registros
                    if (registrosFiltrados.length === 0) {
                        setNoRegistros(true);
                    } else {
                        setNoRegistros(false);
                    }
                }
            })
            .catch((error) => {
                console.error('Error al obtener los registros:', error);
            });
    }, [filtroNombre, filtroNumeroPolicia, filtroFecha]);

    // ...

    return (
        <div className="bitacora">
            <Header className="Header" />
            <div className='main-content'>
                <h2>Bitácora</h2>
                <div className="grafico-linea-container">
                    <div className="grafico-linea">
                        <LinesChartPorDia className="grafico-linea"></LinesChartPorDia>
                    </div>
                    <div className="grafico-linea">
                        <LinesChartPorMes className="grafico-linea"></LinesChartPorMes>
                    </div>
                </div>

                <div className='bitacora-container'>
                    <div className='filtros-container'>
                        <div className="filtro-container">
                            <label htmlFor="filtroNombre">Filtrar por Nombre de Policía: </label>
                            <input
                                type="text"
                                id="filtroNombre"
                                value={filtroNombre}
                                onChange={(e) => setFiltroNombre(e.target.value)}
                            />
                        </div>
                        <div className="filtro-container">
                            <label htmlFor="filtroNumeroPolicia">Filtrar por Número de Policía: </label>
                            <input
                                type="text"
                                id="filtroNumeroPolicia"
                                value={filtroNumeroPolicia}
                                onChange={(e) => setFiltroNumeroPolicia(e.target.value)}
                            />
                        </div>
                        <div className="filtro-container">
                            <label htmlFor="filtroFecha">Filtrar por Fecha:</label>
                            <input
                                type="date"
                                id="filtroFecha"
                                value={filtroFecha}
                                onChange={(e) => setFiltroFecha(e.target.value)}
                            />
                        </div>
                    </div>
                    {noRegistros ? ( // Mostramos mensaje de no registros si no hay datos
                        <div className='no-registros-msg'>No hay registros para mostrar.</div>
                    ) : (
                        <div className="bitacora-tabla-visitantes">
                            <div className="bitacora-table-encabezado bitacora-titulos-tabla">
                                <div className="bitacora-column bitacora-encabezado">Nombre Policia</div>
                                <div className="bitacora-column bitacora-encabezado">N.Policía</div>
                                <div className="bitacora-column bitacora-encabezado">Entrada</div>
                                <div className="bitacora-column bitacora-encabezado">Salida</div>
                                <div className="bitacora-column bitacora-encabezado">Personas</div>
                                <div className="bitacora-column bitacora-encabezado">Ver detalles</div>
                            </div>
                            {registros.map((registro) => (
                                <div className="bitacora-table-encabezado" key={registro.id}>
                                    <div className="bitacora-column">{registro.nombre_policia}</div>
                                    <div className="bitacora-column">{registro.numero_policia}</div>
                                    <div className="bitacora-column">{formatFecha(registro.createdAt)}</div>
                                    <div className="bitacora-column">{formatFecha(registro.updatedAt)}</div>
                                    <div className="bitacora-column">{contarPersonas(registro.personas_familiares)}</div>
                                    <div className="bitacora-column">
                                        <button className="bitacora-btnCheckdetails" onClick={() => mostrarDetallesRegistro(registro)}>
                                            <img className='bitacora-ico' src={detailsIco} alt="Detalles" />
                                        </button>
                                    </div>
                                </div>
                            ))}
                        </div>
                    )}
                </div>
            </div>
            {mostrarDetalles && (
                <RegistroDetalles registro={registroSeleccionado} onClose={cerrarDetalles} />
            )}
        </div>
    );
};

export default Bitacora;
