import React, { useState, useEffect } from 'react';
import './RegistroDetalles.css';

const RegistroDetalles = ({ registro, onClose }) => {
    const [familiares, setFamiliares] = useState([]);

    useEffect(() => {
        console.log(registro);
        if (registro && registro.personas_familiares) {
            // Obtener los IDs de los familiares
            const ids = registro.personas_familiares.split(',');

            // Realizar solicitudes para obtener los detalles de los familiares
            const fetchFamiliares = async () => {
                const detallesFamiliares = [];
                for (const id of ids) {
                    try {
                        const response = await fetch(`http://localhost:3000/api/v1/familiar/${id}`);
                        if (response.ok) {
                            const data = await response.json();
                            detallesFamiliares.push(data);
                        } else {
                            console.error(`Error al obtener el familiar con ID ${id}`);
                        }
                    } catch (error) {
                        console.error(`Error al obtener el familiar con ID ${id}:`, error);
                    }
                }
                setFamiliares(detallesFamiliares);
            };

            fetchFamiliares();
        }
    }, [registro]);

    return (
        <div className="registro-detalles">
            <h2>Detalles del Registro</h2>
            <div className="detalle">
                <p><strong>Nombre Policia:</strong> {registro.nombre_policia}</p>
            </div>
            <div className="familiares">
                <h3>Familiares:</h3>
                {familiares.length > 0 ? (
                    familiares.map((familiar, index) => (
                        <div key={index} className="familiar">
                            <p><strong>Nombre:</strong> {familiar.nombres} {familiar.apellido1} {familiar.apellido2}</p>
                            <p><strong>Observaciones:</strong> {familiar.observacion}</p>
                        </div>
                    ))
                ) : (
                    <p className="familiar">No hay familiares registrados.</p>
                )}
            </div>
            <button className="cerrar-detalles" onClick={onClose}>Cerrar</button>
        </div>
    );
};

export default RegistroDetalles;
