import React, { useState, useEffect } from 'react';
import { useNumeroPolicia } from '../../../../NumeroPoliciaContext'; // Importa el contexto
import './ViewRegistros.css';

const ViewRegistros = () => {
    // Obtiene el número de policía del contexto
    const { numeroPolicia } = useNumeroPolicia();

    // Define un estado para almacenar los datos de la API
    const [data, setData] = useState([]);

    useEffect(() => {
        // Verifica si hay un número de policía antes de hacer la solicitud a la API
        if (numeroPolicia) {
            console.log("numero policia ver datos: " + numeroPolicia)
            // Construye la URL de la API utilizando el número de policía
            const apiUrl = `http://localhost:3000/api/v1/familiar/police/${numeroPolicia}`;

            // Realiza la solicitud a la API con la URL construida
            fetch(apiUrl)
                .then((response) => response.json())
                .then((data) => {
                    // Filtrar los registros eliminados (eliminado: true) y establecer los datos filtrados en el estado
                    const filteredData = data.filter((registro) => !registro.eliminado);
                    setData(filteredData);
                })
                .catch((error) => {
                    console.error('Error al obtener los datos:', error);
                });
        }
    }, [numeroPolicia]); // Asegúrate de incluir [numeroPolicia] en las dependencias

    return (
        <div className="view-registros">
            <h2>Ver Registros</h2>
            {data.length === 0 ? (
                <p>No hay registros para mostrar.</p>
            ) : (
                <div className="view-registros-tabla-visitas">
                    <div className="view-registros-tabla-encabezado view-registros-titulos-tabla">
                        <div className="view-registros-column view-registros-encabezado">Nombre</div>
                        <div className="view-registros-column view-registros-encabezado">observaciones</div>
                        <div className="view-registros-column view-registros-encabezado">Documentos faltantes</div>
                        <div className="view-registros-column view-registros-encabezado">Tipo</div>
                    </div>
                    {data.map((a, index) => (
                        <div className="view-registros-tabla-encabezado" key={index}>
                            <div className="view-registros-column">{a.nombres} {a.apellido1} {a.apellido2}</div>
                            <div className="view-registros-column">{a.observacion}</div>
                            <div className="view-registros-column">
                                {a.tipo_familiar
                                    ? (a.ine === false ? '"INE" ' : '') + (a.certificado_medico === false ? '"Certificado medico" ' : '')
                                    : (a.acta_nacimiento === false ? '"Acta de nacimiento" ' : '') + (a.certificado_medico === false ? '"Certificado medico" ' : '')
                                }
                            </div>
                            <div className="view-registros-column">
                                {a.tipo_familiar ? "Pareja" : "Familiar"}
                            </div>
                        </div>
                    ))}
                </div>
            )}

        </div>
    );
};

export default ViewRegistros;
