import React, { useState, useEffect } from 'react';
import ConfirmPopup from '../../confirmar/ConfirmPopup';
import AlertWindow from '../../alerwindow/AlertWindow';
import { useNumeroPolicia } from '../../../../NumeroPoliciaContext'; // Importa el contexto

import './UpdateRegistros.css';

const UpdateRegistros = () => {

    const { numeroPolicia } = useNumeroPolicia();
    // Define un estado para almacenar los datos de la API
    const [data, setData] = useState([]);
    const [showPopup, setShowPopup] = useState(false);

    const [showAlert, setShowAlert] = useState(false);
    // Supongamos que tienes un array de registros, cada registro es un objeto con los datos de una persona
    useEffect(() => {
        // Verifica si hay un número de policía antes de hacer la solicitud a la API
        if (numeroPolicia) {
            console.log("numero policia ver datos: " + numeroPolicia)
            // Construye la URL de la API utilizando el número de policía
            const apiUrl = `http://localhost:3000/api/v1/familiar/police/${numeroPolicia}`;

            // Realiza la solicitud a la API con la URL construida
            fetch(apiUrl)
                .then((response) => response.json())
                .then((data) => {
                    // Filtrar los registros eliminados (eliminado: true) y establecer los datos filtrados en el estado
                    const filteredData = data.filter((registro) => !registro.eliminado);
                    setData(filteredData);
                })
                .catch((error) => {
                    console.error('Error al obtener los datos:', error);
                });
        }
    }, [numeroPolicia]); // Asegúrate de incluir [numeroPolicia] en las dependencias

    const handleSubmit = (e) => {
        e.preventDefault();

        // Filtrar los registros no eliminados antes de enviarlos al servidor
        const registrosActualizados = data.filter((registro) => !registro.eliminado);

        // Construye un array de promesas para enviar las actualizaciones de cada registro
        const updatePromises = registrosActualizados.map((registro) => {
            const apiUrl = `http://localhost:3000/api/v1/familiar/${registro.id}`; // URL del registro específico
            return fetch(apiUrl, {
                method: 'PUT', // Usar el método PUT para actualizar
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(registro), // Enviar el registro actualizado
            });
        });

        // Utiliza Promise.all para esperar a que todas las solicitudes se completen
        Promise.all(updatePromises)
            .then((responses) => {
                // Verifica las respuestas para asegurarse de que todas fueron exitosas
                const allSuccessful = responses.every((response) => response.ok);

                if (allSuccessful) {
                    // Muestra la ventana de alerta si todas las actualizaciones fueron exitosas
                    setShowAlert(true);
                } else {
                    // Maneja el caso en el que algunas actualizaciones fallaron
                    console.error('Algunas actualizaciones fallaron.');
                }
            })
            .catch((error) => {
                console.error('Error al actualizar los registros:', error);
            });
    };


    // Función para actualizar un registro
    const actualizarRegistro = (id, campo, valor) => {
        // Copiamos los registros actuales
        const nuevosRegistros = [...data];
        // Buscamos el registro por su ID
        const registroActualizado = nuevosRegistros.find(data => data.id === id);

        // Actualizamos el campo correspondiente
        if (registroActualizado) {
            registroActualizado[campo] = valor;
            setData(nuevosRegistros);
        }
    };


    const handleConfirm = () => {
        // Realiza cualquier acción de confirmación que necesites aquí

        // Cierra el popup estableciendo showPopup en false
        setShowPopup(false);

        // Muestra la ventana de alerta
        setShowAlert(true);
    };

    const handleCancel = () => {
        // Lógica para cancelar acción
        setShowPopup(false);
    };

    return (
        <div className="update-registros">
            <h2>Actualizar Registros</h2>
            {data.length === 0 ? (
                <p>No hay registros para actualizar.</p>
            ) : (
                <form onSubmit={handleSubmit}><div className="update-registros-tabla-visitas">
                    <div className="update-registros-tabla-encabezado update-registros-titulos-tabla">
                        <div className="update-registros-column update-registros-encabezado">Nombre:</div>
                        <div className="update-registros-column update-registros-encabezado">apellido 1:</div>
                        <div className="update-registros-column update-registros-encabezado">apellido 2:</div>
                        <div className="update-registros-column update-registros-encabezado">Observación:</div>
                        <div className="update-registros-column update-registros-encabezado">INE</div>
                        <div className="update-registros-column update-registros-encabezado">Certificado medico</div>
                        <div className="update-registros-column update-registros-encabezado">Acta de nacimiento</div>
                        <div className="update-registros-column update-registros-encabezado">Tipo:</div>
                    </div>
                    {data.map(a => (
                        <div className="update-registros-tabla-encabezado" key={a.id}>
                            <div className="update-registros-column">
                                <input
                                    className='update-registros-input'
                                    type="text"
                                    value={a.nombres}
                                    onChange={(e) => actualizarRegistro(a.id, 'nombres', e.target.value)}
                                />
                            </div>
                            <div className="update-registros-column">
                                <input
                                    className='update-registros-input'
                                    type="text"
                                    value={a.apellido1}
                                    onChange={(e) => actualizarRegistro(a.id, 'apellido1', e.target.value)}
                                />
                            </div>
                            <div className="update-registros-column">
                                <input
                                    className='update-registros-input'
                                    type="text"
                                    value={a.apellido2}
                                    onChange={(e) => actualizarRegistro(a.id, 'apellido2', e.target.value)}
                                />
                            </div>
                            <div className="update-registros-column">
                                <input
                                    className='update-registros-input'
                                    type="text"
                                    value={a.observacion}
                                    onChange={(e) => actualizarRegistro(a.id, 'observacion', e.target.value)}
                                />
                            </div>
                            <div className="update-registros-column">
                                <input
                                    className='update-checkbox-input'
                                    type="checkbox"
                                    checked={a.ine}
                                    disabled={!a.tipo_familiar} // Deshabilitar si es "Pareja"
                                    onChange={(e) => actualizarRegistro(a.id, 'ine', e.target.checked)}
                                />
                            </div>
                            <div className="update-registros-column">
                                <input
                                    className='update-checkbox-input'
                                    type="checkbox"
                                    checked={a.certificado_medico}
                                    onChange={(e) => actualizarRegistro(a.id, 'certificado_medico', e.target.checked)}
                                />
                            </div>
                            <div className="update-registros-column">
                                <input
                                    className='update-checkbox-input'
                                    type="checkbox"
                                    checked={a.acta_nacimiento}
                                    disabled={a.tipo_familiar} // Deshabilitar si no es "Pareja"
                                    onChange={(e) => actualizarRegistro(a.id, 'acta_nacimiento', e.target.checked)}
                                />
                            </div>
                            <div className="update-registros-column">
                                {a.tipo_familiar ? "Pareja" : "Familiar"}
                            </div>
                        </div>
                    ))}
                </div>
                    <button className='update-submit-button' type="submit">
                        Actualizar Registros
                    </button>
                    {showPopup && (
                        <ConfirmPopup
                            message="¿Actualizar registros?"
                            onConfirm={() => handleConfirm()} // Llamar handleConfirm cuando se confirma en el popup
                            onCancel={handleCancel}
                        />
                    )}{showAlert && (
                        <AlertWindow
                            message="El registro se guardó correctamente."
                            onClose={() => setShowAlert(false)} // Cerrar la ventana de alerta cuando sea necesario
                        />
                    )}
                </form>
            )
            }
        </div >
    );
};

export default UpdateRegistros;
