import React, { useState, useEffect } from 'react';

import ConfirmPopup from '../../confirmar/ConfirmPopup';
import DeleteIco from '../../../assets/delete2.png'
import { useNumeroPolicia } from '../../../../NumeroPoliciaContext'; // Importa el contexto

import './DeleteRegistros.css'

const DeleteRegistros = () => {
    const { numeroPolicia } = useNumeroPolicia();
    const [data, setData] = useState([]);
    const [showPopup, setShowPopup] = useState(-1); // Inicializamos con -1
    const [showDeletePopup, setShowDeletePopup] = useState(false); // Controla la visualización del mensaje de confirmación de eliminación
    const [deletedIndexes, setDeletedIndexes] = useState([]); // Almacena los índices de registros que se eliminarán

    useEffect(() => {
        if (numeroPolicia) {
            const apiUrl = `http://localhost:3000/api/v1/familiar/police/${numeroPolicia}`;
            fetch(apiUrl)
                .then((response) => response.json())
                .then((data) => {
                    // Filtrar los registros eliminados (eliminado: true) y establecer los datos filtrados en el estado
                    const filteredData = data.filter((registro) => !registro.eliminado);
                    setData(filteredData);
                })
                .catch((error) => {
                    console.error('Error al obtener los datos:', error);
                });
        }
    }, [numeroPolicia]);

    const handleConfirm = (index) => {
        // Agregar el índice al array de registros a eliminar
        setDeletedIndexes([...deletedIndexes, index]);
        setShowDeletePopup(true);
        setShowPopup(-1); // Volvemos a establecer -1 para cerrar el popup de eliminación individual
    };

    const handleCancel = () => {
        setShowDeletePopup(false);
        setDeletedIndexes([]); // Limpiar los registros a eliminar
    };

    const handleDelete = () => {
        // Realizar una solicitud PUT para actualizar el estado "eliminado" en la base de datos a true
        // Utiliza el array de "deletedIndexes" para identificar los registros a eliminar

        // Por cada índice en deletedIndexes, actualiza el estado "eliminado" a true en la base de datos
        deletedIndexes.forEach((index) => {
            const recordToDelete = data[index];

            // Realizar la solicitud PUT para marcar el registro como eliminado
            fetch(`http://localhost:3000/api/v1/familiar/${recordToDelete.id}`, {
                method: 'PUT',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ eliminado: true }), // Marcar el registro como eliminado
            })
                .then((response) => response.json())
                .then((result) => {
                    // Eliminar el registro del estado "data"
                    const updatedData = data.filter((_, i) => i !== index);
                    setData(updatedData);
                })
                .catch((error) => {
                    console.error('Error al eliminar el registro:', error);
                });
        });

        // Limpiar el array de "deletedIndexes" y cerrar el mensaje de confirmación de eliminación
        setDeletedIndexes([]);
        setShowDeletePopup(false);
    };

    return (
        <div className="delete-registros">
            <h2>Eliminar Registro</h2>
            {data.length === 0 ? (
                <p>No hay registros para eliminar.</p>
            ) : (
                <div className="delete-registros-tabla-visitas">
                    <div className="delete-registros-tabla-encabezado delete-registros-titulos-tabla">
                        <div className="delete-registros-column delete-registros-encabezado">Nombre</div>
                        <div className="delete-registros-column delete-registros-encabezado">Observación</div>
                        <div className="delete-registros-column delete-registros-encabezado">Documentos faltantes</div>
                        <div className="delete-registros-column delete-registros-encabezado">Tipo</div>
                        <div className="delete-registros-column delete-registros-encabezado">Eliminar</div>
                    </div>
                    {data.map((a, index) => (
                        <div className="delete-registros-tabla-encabezado" key={index}>
                            <div className="delete-registros-column">{a.nombres} {a.apellido1} {a.apellido2}</div>
                            <div className="delete-registros-column">{a.observacion}</div>
                            <div className="delete-registros-column">
                                {a.tipo_familiar
                                    ? (a.ine === false ? '"INE" ' : '') + (a.certificado_medico === false ? '"Certificado medico" ' : '')
                                    : (a.acta_nacimiento === false ? '"Acta de nacimiento" ' : '') + (a.certificado_medico === false ? '"Certificado medico" ' : '')
                                }
                            </div>
                            <div className="delete-registros-column">{a.tipo_familiar ? "Pareja" : "Familiar"}</div>
                            <div className="delete-main-column">
                                <button className="delete-main-btn-delete" onClick={() => handleConfirm(index)}>
                                    <img className='delete-main-ico' src={DeleteIco} alt="Eliminar Registro"></img>
                                </button>
                                {showPopup === index && (
                                    <ConfirmPopup
                                        message="¿Seguro que desea eliminar el registro?"
                                        onConfirm={() => handleConfirm(index)}
                                        onCancel={handleCancel}
                                    />
                                )}
                            </div>
                        </div>
                    ))}
                </div>
            )}
            {showDeletePopup && (
                <ConfirmPopup
                    message="¿Seguro que desea eliminar los registros seleccionados?"
                    onConfirm={handleDelete}
                    onCancel={handleCancel}
                />
            )}
        </div>
    );
};

export default DeleteRegistros;
