import React from 'react';
import './AlertWindow.css';

const AlertWindow = ({ message, onClose }) => {
    return (
        <div className="alert-window-container">
            <div className="alert-window-content">
                <p className="alert-window-message">{message}</p>
                <button className="alert-window-button" onClick={onClose}>
                    Aceptar
                </button>
            </div>
        </div>
    );
};

export default AlertWindow;
