import React, { useState, useEffect } from 'react';
import { Line } from 'react-chartjs-2';
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend,
    Filler,
} from 'chart.js';

ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend,
    Filler
);

export default function LinesChartPorMes() {
    const [data, setData] = useState([]);
    const [meses, setMeses] = useState([]);
    const [registrosPorMes, setRegistrosPorMes] = useState([]);

    useEffect(() => {
        // Realiza la solicitud GET a la API para obtener los registros del año actual
        fetch('http://localhost:3000/api/v1/casapolicia')
            .then((response) => response.json())
            .then((data) => {
                // Filtra los registros del año actual
                const registrosDelAnio = data.filter((registro) => {
                    const fecha = new Date(registro.updatedAt);
                    const anioActual = new Date().getFullYear();
                    return fecha.getFullYear() === anioActual;
                });

                // Obtener los datos necesarios para el gráfico
                const meses = [
                    'Enero', 'Febrero', 'Marzo', 'Abril', 'Mayo', 'Junio',
                    'Julio', 'Agosto', 'Septiembre', 'Octubre', 'Noviembre', 'Diciembre'
                ];
                const registrosPorMes = Array(12).fill(0);
                registrosDelAnio.forEach((registro) => {
                    const fecha = new Date(registro.updatedAt);
                    const mes = fecha.getMonth();
                    registrosPorMes[mes] += 1;
                });
                setMeses(meses);
                setRegistrosPorMes(registrosPorMes);
                setData(registrosDelAnio);
            })
            .catch((error) => {
                console.error('Error al obtener los datos:', error);
            });
    }, []);

    const chartData = {
        labels: meses.map((mes) => `${mes}`),
        datasets: [
            {
                label: 'Registros por mes del año',
                data: registrosPorMes,
                tension: 0.5,
                fill: true,
                borderColor: 'rgb(0, 128, 0)', // Cambiar a verde
                backgroundColor: 'rgba(0, 128, 0, 0.5)', // Cambiar a verde
                pointRadius: 5,
                pointBorderColor: 'rgba(0, 128, 0)', // Cambiar a verde
                pointBackgroundColor: 'rgba(0, 128, 0)', // Cambiar a verde
            },
        ],
    };

    const chartOptions = {
        scales: {
            y: {
                min: 0,
            },
            x: {
                ticks: { color: 'rgba(0, 128, 0)' }, // Cambiar a verde
            },
        },
    };

    return <Line data={chartData} options={chartOptions} />;
}
