import React, { useState } from 'react';
import AlertWindow from '../alerwindow/AlertWindow';
import { useNumeroPolicia } from '../../../NumeroPoliciaContext'; // Importa el hook del contexto
import './ScannerInput.css';

const ScannerInput = ({ onDataSubmit }) => {
    const [inputValue, setInputValue] = useState('');
    const [mostrarAlerta, setMostrarAlerta] = useState(false);

    // Accede al contexto para obtener y actualizar numero_policia y nombre_policia
    const { setNumeroPolicia, setNombrePolicia } = useNumeroPolicia();

    const handleInputChange = (event) => {
        setInputValue(event.target.value);
    };

    const handleCloseAlert = () => {
        setMostrarAlerta(false);
    };

    const handleSubmit = (event) => {
        event.preventDefault();

        const parts = inputValue.split("]]"); // Dividir la cadena utilizando "]]" como separador
        if (parts.length === 3) {
            const ID_Trabajador = parts[0];
            const nombrePolicia = parts[1];
            setNumeroPolicia(ID_Trabajador);
            setNombrePolicia(nombrePolicia);

            // Llama a la función onDataSubmit pasando el ID_Trabajador
            onDataSubmit(ID_Trabajador);
        } else {
            setMostrarAlerta(true);
        }
    };

    return (
        <div className="scanner-input-container">
            <form onSubmit={handleSubmit}>
                <div className='scanner-input-label'>Escanea el código QR para continuar</div>
                <input
                    className='scanner-input-input'
                    type="text"
                    placeholder="Escanear código QR o digitar el número de empleado"
                    value={inputValue}
                    onChange={handleInputChange}
                />
                <button className='scanner-input-button' type="submit">Enviar</button>
            </form>
            {mostrarAlerta && (
                <AlertWindow
                    message="Número de empleado incorrecto"
                    onClose={handleCloseAlert}
                />
            )}
        </div>
    );
};

export default ScannerInput;
