import React, { useState, useEffect } from 'react';
import ParejaForm from '../../parejaForm/ParejaForm';
import FamiliarForm from '../../FamiliarForm/FamiliarForm';
import { useNumeroPolicia } from '../../../../NumeroPoliciaContext'; // Importa el contexto

import './CreateRegistro.css';

const CreateRegistro = ({ onSubmit }) => {
    const [flagOfPareja, setFlagOfPareja] = useState(true); // Por defecto, la bandera está en true
    const [numberOfPeople, setNumberOfPeople] = useState(1);
    const [names, setNames] = useState([]);

    const { numeroPolicia, nombrePolicia } = useNumeroPolicia();
    useEffect(() => {
        // Realiza la consulta a la API utilizando el número de policía
        if (numeroPolicia) {
            fetch(`http://localhost:3000/api/v1/familiar/tipoFamiliarAndNumeroPolicia/${numeroPolicia}`)
                .then((response) => response.json())
                .then((data) => {
                    if (data.length > 0) {
                        // Verifica si hay al menos una pareja registrada y no eliminada
                        const algunaParejaNoEliminada = data.some((registro) => registro.tipo_familiar && !registro.eliminado);
                        console.log("¿Algún registro de pareja no eliminado? " + algunaParejaNoEliminada);
                        if (algunaParejaNoEliminada) {
                            // Hay al menos una pareja registrada y no eliminada
                            setFlagOfPareja(false);
                        } else {
                            // No hay pareja registrada o todas están eliminadas
                            setFlagOfPareja(true);
                        }
                    }
                })
                .catch((error) => {
                    console.error('Error al obtener datos de la consulta:', error);
                });
        }
    }, [numeroPolicia]);


    const handleSubmit = (event) => {
        event.preventDefault();
        onSubmit(names);
    };

    const handleNameChange = (index, field, value) => {
        const updatedNames = [...names];
        updatedNames[index] = {
            ...updatedNames[index],
            [field]: value,
        };
        setNames(updatedNames);
    };

    const handleAddPareja = () => {
        setFlagOfPareja(true);
        setNames([]);
    };

    const handleAddFamiliar = () => {
        setNumberOfPeople(numberOfPeople + 1);
        setNames([...names, {}]);
    };

    const handleRemoveFamiliar = (index) => {
        const updatedNames = names.filter((_, i) => i !== index);
        setNumberOfPeople(updatedNames.length);
        setNames(updatedNames);
    };

    const renderParejaInputs = () => {
        // Comprobar si ya está registrada una pareja en la bd, si no la hay, mostrar el mensaje o el formulario
        if (flagOfPareja) {
            return (
                <div>
                    <ParejaForm index={0} names={names} handleNameChange={handleNameChange} />
                </div>
            );
        } else {
            return (
                <div className="names-form-pareja-exists">
                    Ya existe una pareja registrada.
                </div>
            );
        }
    };

    const renderFamiliarInputs = () => {
        const nameInputs = [];
        for (let i = 0; i < numberOfPeople; i++) {
            nameInputs.push(
                <FamiliarForm
                    key={i}
                    index={i}
                    names={names}
                    handleNameChange={handleNameChange}
                    handleRemove={handleRemoveFamiliar}
                />
            );
        }
        return nameInputs;
    };

    return (
        <form className="names-form-container" onSubmit={handleSubmit}>
            <div className='names-form-pareja-container'>
                {renderParejaInputs()}
            </div>
            <div className='names-form-familiares-container'>
                {renderFamiliarInputs()}
                <div className='names-form-fam-cont-buttons'>
                    {numberOfPeople > 0 && (
                        <div>
                        </div>
                    )}
                    <div>
                        <button className="names-form-button-add" type="button" onClick={handleAddFamiliar}>
                            Agregar Familiar
                        </button>
                    </div>
                </div>
            </div>
        </form>

    );
};

export default CreateRegistro;
