import React, { useState } from 'react';
import './Login.css';
import Logo from '../assets/svg/policia.svg';
import { useNavigate } from 'react-router-dom';

async function loginUser(credentials) {
    const url = `${process.env.REACT_APP_PROTOCOL}://${process.env.REACT_APP_URL}:${process.env.REACT_APP_PORT}${process.env.REACT_APP_PATH}/auth/signin`;

    return fetch(url, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify(credentials)
    })
        .then(data => data.json())
        .catch((error) => {
            console.log('Hubo un problema con la petición Fetch:' + error.message);
        });
}


const Login = ({ setAccessToken, handleLogin }) => {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [errorMessage, setErrorMessage] = useState('');

    const navigate = useNavigate();
    const [error, setError] = useState(false);
    const [errorGeneral, setErrorGeneral] = useState(false);
    const handleFormSubmit = async (e) => {
        console.log("usuario: " + username + " contraseña: " + password)
        e.preventDefault();
        if (username === "" || password === "") {
            setError(true);
            setErrorMessage('Por favor, complete ambos campos de inicio de sesión.');
            return;
        }
        try {
            const response = await loginUser({
                "username": username,
                "password": password
            });
            if (response && response.status === 401) {
                setErrorGeneral(true);
                setUsername("");
                setPassword("");
                setError(false);
            } else {
                setError(false);
                sessionStorage.setItem("accessToken", response.accessToken);
                sessionStorage.setItem("expires_in", Number(Date.now()) + Number(response.expires_in));
                setAccessToken(response.accessToken);
                navigate('/main');
                console.log("todo perfecto padrino, eres la verga bro :D")
            }
        } catch (error) {
            console.error('Hubo un error en la solicitud HTTP:', error);
            setErrorMessage('Error al iniciar sesion, intentelo nuevamente');
        }
        setError(true);
        setErrorMessage('Usuario o contraseña incorrectos');
    };

    return (
        <div className="login-container">
            <h1 className='login-title'>Visitas Casa Policia Morelia</h1>
            <div className="login-card">
                <img className="login-logo" src={Logo} alt="logo" />
                <h2>Iniciar Sesión</h2>
                <form onSubmit={handleFormSubmit}>
                    <div className="form-group">
                        <label>Usuario:</label>
                        <input
                            type="text"
                            value={username}
                            onChange={(e) => setUsername(e.target.value)}
                        />
                    </div>
                    <div className="form-group">
                        <label>Contraseña:</label>
                        <input
                            type="password"
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                        />
                    </div>
                    {errorMessage && <p className="error-message">{errorMessage}</p>}
                    <button className="login-button" type="submit">Iniciar Sesión</button>
                </form>
            </div>
        </div>
    );
};

export default Login;
